.cats-selector {
  input {
    margin: 0;
    padding: 0;
    appearance: none; } }

.input-hidden {
  position: absolute;
  left: -9999px; }

input[type=radio]:checked + label>img {
  border: 1px solid #fff;
  box-shadow: 0 0 5px 5px rgba(200, 200, 200, 10); }

input[type=radio] + label>img {
  max-width: 100%;
  height: 70%;
  transition: 500ms all; }

img.unchecked {
  filter: brightness(1.8) grayscale(1) opacity(.7);
  transition: 1s all;
  &:hover {
    filter: brightness(1) grayscale(0.5) opacity(1); } }


